$mainColor: lightCoral;
$mainTranspColor: rgba(240, 128, 128, 0.925);
$lightColor: white;
$darkColor: black;
$accent1Color: lightCoral;
$accent2Color: rgb(89, 207, 89);
$linkColor: lightblue;
$topBarLinkColor: black;
$background1Color: lightCoral;
$background2Color: white;


$headerFonts: 'Amatic SC', sans-serif;
$header2Fonts: 'Amatic SC', sans-serif;
$cardHeadFont: 'Amatic SC', sans-serif;

$topBarHeight: 60px;
$pageHeight: calc(100vh - 60px);
$sidePadding: calc(0.03 * 100vw);
$topBottomPadding: 30px;
//@import '../../global.scss/';

$mobileWidth: 768px;

@mixin mobile {
    @media (max-width: #{$mobileWidth}){
        @content
    }
}

h2, h3, h4, h5, h6 {
  font-family: $header2Fonts;
  //font-family: 'Libre Baskerville', serif;
  //font-family: 'Open Sans', sans-serif;
  //font-family: 'Source Sans Pro', sans-serif;
}

a, Link {
  color: $linkColor;
  font-family: $header2Fonts;
  font-weight: bold;
}

h1 {
  font-family: $headerFonts;
  font-size: 60px;
  font-style: bold;
  @include mobile{
    font-size: 40px;
  }
}
h2 {
  font-size: 40px;
  @include mobile{
    font-size: 35px;
  }
}
h3 {
  font-size: 30px;
}

h4 {
  font-size: 25px;
}

h5 {
  font-size: 20px;
}

.cards {
  display: flex;
  flex-direction: column;
  justify-content: center;
  @include mobile {
    justify-content: center;
  }
  .container {
    width: 100vw;
    height: 100%;
    flex-direction: column;
    display: flex;
    align-items: center;
    justify-content: center;
    .item {
      width: 700px;
      height: 250px;
      background-color: $lightColor;
      border-radius: 20px;
      display: flex;
      align-items: center;
      justify-content: center;
      overflow: hidden;
      margin: 15px 0;

      @include mobile {
        width: 80%;
        height: 150px;
      }
      .left {
        flex: 8;
        height: 80%;
        display: flex;
        align-items: center;
        justify-content: center;
        .leftContainer {
          width: 90%;
          height: 70%;
          display: flex;
          flex-direction: column;
          justify-content: center;
            .imgContainer {
              width: 40px;
              height: 40px;
              border-radius: 50%;
              //background-color: rgb(245, 179, 155);
              display: flex;
              align-items: center;
              justify-content: center;
              @include mobile {
                width: 20px;
                height: 20px;
              }
              img {
                width: 25px;
                @include mobile {
                  width: 15px;
                }
              }
            }
          
          h2 {
            font-family: $cardHeadFont;
            font-size: 40px;
            //margin: 5px 0;
            @include mobile{
              font-size: 25px;
            }
          }
          h3 {
            font-family: $cardHeadFont;
            font-size: 30px;
            margin: 10px 0;
            color: $accent2Color;
            @include mobile{
              font-size: 20px;
            }
          }
          h4 {
            font-family: $cardHeadFont;
            font-size: 20px;
            margin-bottom: 5px;
            @include mobile{
              margin-bottom: 0;
              font-size: 16px;
            }
          }
          p {
            font-size: 18px;
          }
          span {
            font-size: 14px;
            font-weight: 600;
            text-decoration: underline;
            cursor: pointer;
          }
        }
      }
      .right {
        flex: 4;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
        overflow: hidden;
        img {
          width: 100%;
        }
      }
    }
  }
}


.textAndPicture {
  margin: $topBottomPadding;
  display: flex;
  flex-direction: row;
  align-items: center;
  overflow-x: hidden;
  width:100%;
  @include mobile {
    flex-direction: column;
  }
  .left {
    flex: 2;
    overflow: hidden;
    margin: 20px;
    img {
      height: 100%;
      @include mobile {
        height: 150px;
      }
    }
  }
  .right {
    flex: 3;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: space-between;
    @include mobile {
      align-items: center;
    }
    p {
      font-size: 20px;
      padding: 10px 30px;
    }
    .horizList {
      flex-direction: row;
      padding: 10px 30px;
      .title {
        font-weight: bold;
      }
      p {
        padding: 0;
      }
    }
    .links {
      h3 {
        margin: 0 10px;
      }
      display: flex;
      flex-direction: row;
      align-items: center;
      padding: 10px 20px;
      .icon {
        font-size: 30px;
        margin: 0 10px;
      }
      a {
        font-size: 30px;
        text-decoration: none;
        color: $topBarLinkColor;
        //margin: 0 10px;
      }
      a:hover {
        color: $linkColor;
      }
    }
  }
}